import { PAGE_SIZE } from '@/assets/config'
import { CODE, SelectDataSource, periodicalPaperChineseKeys } from '@/assets/constant'
import tabsService from '@/assets/template/tabs'
import utilizeService from '@/assets/template/utilize'
import dataService from '@/assets/method/data'
import Cache from '@/assets/cache'
import Service from '@/assets/service/service'
export default {
  name: 'periodicalPaperChinese',
  data () {
    return {
      code: CODE.PeriodicalPaperChinese,
      dataKeys: periodicalPaperChineseKeys,
      items: [],
      total: 0,
      page: 1,
      pageSize: PAGE_SIZE,
      operation: {},
      service: Service,
      condition: {
        page_num: PAGE_SIZE
      },
      searchOptions: [
        { label: '出版发表时间', key: 'publishing_time', type: 'date' },
        { label: '成果名称', key: 'name' },
        { label: '第一作者', key: 'first_author' },
        { label: '第一作者身份', key: 'first_author_identity', selectOption: SelectDataSource.Authorship },
        { label: '通讯作者', key: 'communication_author' },
        { label: '通讯作者身份', key: 'communication_author_identity', selectOption: SelectDataSource.Authorship },
        { label: '全部作者', key: 'all_author' },
        { label: '在全部作者中的排名', key: 'rank_all_author' },
        { label: '成果来源', key: 'source_of_result', selectOption: SelectDataSource.ResultsSource },
        { label: '发表刊物全称', key: 'full_name' },
        { label: '卷号', key: 'volume_number' },
        { label: '转载及收录情况', key: 'reprint', selectOption: SelectDataSource.ReprintedInformation },
        { label: '成果等级', key: 'result_level', selectOption: SelectDataSource.AchievementGrade },
        { label: '业绩归属', key: 'performance_attribution' },
        { label: '通讯作者署名单位', key: 'unit' }
      ]
    }
  },
  methods: {
    publicSearch (condition) {
      for (const key in condition) {
        this.condition[key] = condition[key]
      }
      this.getDataTables(this.page)
    },
    search () {
      this.getDataTables()
    },
    pageChange (page) {
      this.getDataTables(page)
    },
    getDataTables (page = 1) {
      this.pageSize = this.condition.page_num
      const request = {

      }
      if (page) {
        request['page'] = page
      }
      for (const key in this.condition) {
        if (this.condition[key]) {
          request[key] = this.condition[key]
        }
      }

      Service.PeriodicalPaperChinese.get(request).then(response => {
        if (response.code == 200) {
          this.page = page
          this.items = response.data.data
          this.total = response.data.total
          setTimeout(() => utilizeService.iCheck(), 200)
        }
      })
    },
    download (item) {
      this.fileExport({
        ids: [item.id],
        model: this.code
      })
    },
    fileExport (request) {
      Service.FileDownload(request).then(response => {
        dataService.download(response)
      })
    },
    delConfirm (item) {
      Service.PeriodicalPaperChinese.del(item.id).then(response => {
        if (response.code == 200) {
          this.getDataTables()
        }
      })
    },
    add () {
      tabsService.addDetailsTab('/views/periodicalPaper/periodicalPaperChinese/new', '期刊论文-中文-新增')
    },
    edit (item) {
      tabsService.addDetailsTab('/views/periodicalPaper/periodicalPaperChinese/' + item.id, '期刊论文-中文-详情-' + item.id)
    }
  },
  created () {
    this.operation = Cache.pageOperation('academic_achievements')
    dataService.getTableKeys(Service, this.code, this.dataKeys).then(dataKeys => {
      this.dataKeys = dataKeys
    })
    if (this.$route.query['key']) {
      this.condition['key'] = this.$route.query['key'];this.condition['status'] = 3
    }
    this.getDataTables()
  },
  mounted () {
  }
}



